* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 10.22px;
}

@media (min-width: 760px) {
  :root {
    font-size: 16px;
  }
}

body {
  font-family: "Manrope", sans-serif;
  text-transform: uppercase;
}

h1 {
  font-size: 3.5rem;
  letter-spacing: 0.125rem;
  line-height: 3.625rem;
}

h2 {
  font-size: 2.5rem;
  letter-spacing: 0.0938rem;
  line-height: 2.75rem;
}

h3 {
  font-size: 2rem;
  letter-spacing: 0.0719rem;
  line-height: 2.25rem;
}

h4 {
  font-size: 1.74rem;
  letter-spacing: 2px;
}

h5 {
  font-size: 1.5rem;
  letter-spacing: 0.1062rem;
  line-height: 2.0625rem;
}

h6 {
  font-size: 18px;
  letter-spacing: 1.29px;
  line-height: 44px;
  font-weight: bold;
}

.link {
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  text-decoration: none;
  text-align: center;
}

.overline {
  font-size: 14px;
  letter-spacing: 10px;
}

.textContent {
  font-size: 15px;
  font-weight: 200;
  text-transform: none;
  line-height: 25px;
}

.white75 {
  color: hsla(0, 0%, 100%, 0.75);
}

.white50 {
  color: hsla(0, 0%, 100%, 0.5);
}

.white100 {
  color: white;
}

.black50 {
  color: hsla(0, 0%, 0%, 0.5);
}

.black100 {
  color: hsl(0, 0%, 0%);
}

.orange {
  color: #d87d4a;
}

.alignLeft {
  text-align: left;
}

.alignCenter {
  text-align: center;
}

.alignRight {
  text-align: right;
}

@media (min-width: 1400px) {
  .alignDesktopLeft {
    text-align: left;
  }
}
